@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: $font-family;
  font-display: swap;
  src:
    local("Lato"),
    url("../fonts/lato-regular.woff2") format("woff2"),
    url("../fonts/lato-regular.woff") format("woff");
}

@font-face {
  font-style: italic;
  font-weight: 400;
  font-family: $font-family;
  font-display: swap;
  src:
    local("Lato"),
    url("../fonts/lato-regular-italic.woff2") format("woff2"),
    url("../fonts/lato-regular-italic.woff") format("woff");
}

