.social-links {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 0;
  padding: 0;
  list-style: none;
}

.social-links__link {
  display: block;
  width: 32px;
  height: 32px;

  &:focus,
  &:hover {
    opacity: 0.7;
  }
}

.social-links__link svg {
  width: 100%;
  height: 100%;
  fill: currentColor;
}
